import { useCallback, useEffect, useState } from 'react';
import { getDrinks } from 'Utils/data';
import { Drink } from 'Types/index';

const useFetchDrinks = () => {
	const [data, setData] = useState<Drink[]>();
	const [loading, setLoading] = useState(false);
	const [error, setError] = useState();

	const fetchData = async () => {
		setLoading(true);
		try {
			const res = await getDrinks();
			setData(res);
			// DOTO: remove any
		} catch (err: any) {
			setError(err);
		} finally {
			setLoading(false);
		}
	};

	const update = useCallback((itemId: number, sold: boolean) => {
		if (data) {
			const index = data?.findIndex(({ id }) => id === itemId);
			setData([
				...data?.slice(0, index),
				{ ...data[index], sold },
				...data?.slice(index + 1, data.length),
			]);
		}
	}, [data]);

	useEffect(() => {
		fetchData();
	}, []);

	return { data, loading, error, update };
};

export default useFetchDrinks;